body .is-layout-constrained .wp-block-blocks-gamestore-block-hero {
  width: 100vw!important;
  height: 980px;
  max-width: 100vw!important;
  margin-right: calc(var(--wp--style--root--padding-right) * -1)!important;
  margin-left: calc(var(--wp--style--root--padding-left) * -1)!important;
  position: relative;
}
.wp-block-blocks-gamestore-block-hero {
  .image-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 980px;
    object-fit: cover;
    z-index: 0;
  }
  .video-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 980px;
    object-fit: cover;
    z-index: 0;
  }
  .hero-mask {
    width: 80%;
    height: 100%;
    display: block;
    position: absolute;
    top:0;
    left:0;
    z-index: 1;
    background: var(--mask-gradient);
  }
  .hero-content {
    max-width: 1540px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 24px;
    position: relative;
    z-index: 2;
    padding-left: var(--wp--style--root--padding-left);
    padding-right: var(--wp--style--root--padding-right);

    .hero-title {
      color: var(--white-contrast);

      strong {
        background: var(--accent-gradient, linear-gradient(90deg, #3DAFE2 0%, #6F76C0 100%));
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }
  .hero-slider {
    z-index: 3;
    width: 100%;
    position: absolute;
    left:0;
    bottom: 0;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--mask-gradient-second, linear-gradient(270deg, rgba(250, 250, 250, 0.00) 0%, #FAFAFA 100%));
  
    .slider-container {
      width: 100%;
      max-width: 1540px;
      margin-left: auto;
      margin-right: auto;
      overflow: hidden;
      box-sizing: border-box;

      .swiper-wrapper {
        display: flex;

        .slide-item {
          margin-right: 100px;
          max-width: fit-content;
          margin-left: 0;

          img.dark-logo {
            display: none;
          }
        }
      }
    }
  }
}
.dark-mode-gamestore {
  .wp-block-blocks-gamestore-block-hero .hero-slider .slider-container .swiper-wrapper .slide-item {
    img.dark-logo {
      display: block;
    }
    img.light-logo {
      display: none;
    }
  } 
}